<template>
  <div class="home">
    <!-- 表单 -->
    <div class="home_form">

      <el-form class="form" :inline="true" label-width="100px">
        <el-form-item label="类别名称:">
          <el-input  placeholder="请输入类别名称" v-model.trim="categoryName" clearable></el-input>
        </el-form-item>
        <el-form-item label="父类别:">
          <el-select v-model="parentId" placeholder="请选择父类别" @change="changeCategor(parentId)">
            <el-option label="全部父类别" value="0"></el-option>
            <el-option
              v-for="item in parentCategory"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search"><i class="el-icon-search"> 查询</i></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="info" @click="addDialog = true"><i class="el-icon-plus"> 添加</i></el-button>
        </el-form-item>
      </el-form>

    </div>

    <!-- 添加表单 -->
    <el-dialog title="新增食品类别" :visible.sync="addDialog" width="30%">
      <span>
        <el-form class="add_form" ref="add_form" :model="add_form" :rules="rules" label-width="100px">
          <el-form-item label="父类别:" prop="parentId">
            <el-select v-model="add_form.parentId" placeholder="请选择父类别" style="width: 100%;">
              <el-option label="全部父类别" value="0"></el-option>
              <el-option
                v-for="item in parentCategory"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="类别名称:" prop="name">
            <el-input v-model="add_form.name" clearable></el-input>
          </el-form-item>
          <el-form-item label="类别描述:" prop="categoryDescription">
            <el-input v-model="add_form.categoryDescription" clearable></el-input>
          </el-form-item>
        </el-form>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeAddDialog">取 消</el-button>
        <el-button type="primary" @click="add(add_form)">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 表格 -->
    <div class="home_table">

      <el-table class="table" :data="tableData" border>
        <el-table-column fixed prop="category" label="父类别名称"></el-table-column>
        <el-table-column prop="name" label="类别名称"></el-table-column>
        <el-table-column prop="categoryDescription" label="类别描述"></el-table-column>
        <el-table-column fixed="right" label="操作">
          <template slot-scope="scope">
            <!-- 编辑 -->
            <el-button type="primary" size="small" @click="openEditDialog(scope.row)">编辑</el-button>
            <!-- 移除 -->
            <el-button @click="openMesBox(scope.row)" type="danger" size="small">移除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 编辑表单 -->
    <el-dialog title="更新食品类别" :visible.sync="editDialog" width="30%">
      <span>
        <el-form class="edit_form" ref="edit_form" :model="edit_form" :rules="rules" label-width="100px">
          <el-form-item label="父类别:" prop="parentId">
            <el-select v-model="edit_form.parentId" placeholder="请选择父类别" style="width: 100%;">
              <el-option label="全部父类别" value="0"></el-option>
              <el-option
                v-for="item in parentCategory"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="类别名称:" prop="name">
            <el-input v-model="edit_form.name" clearable></el-input>
          </el-form-item>
          <el-form-item label="类别描述:" prop="categoryDescription">
            <el-input v-model="edit_form.categoryDescription" clearable></el-input>
          </el-form-item>
        </el-form>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialog = false">取 消</el-button>
        <el-button type="primary" plain @click="edit(edit_form)">确 定</el-button>
      </span>
    </el-dialog>
    <el-pagination
      @current-change="Pagination"
      :current-page.sync="paginator.currentPage"
      :page-size="paginator.pageSize"
      :total="paginator.totalRecord"
      layout="total, prev, pager, next" >
    </el-pagination>
</div>
</template>

<script>
import { Notification } from 'element-ui'

import axios from 'axios'

export default {
  name: 'Home',
  data () {
    return {
      categoryName: '', // 类别名称
      parentId: '0', // 默认父类别选项为全部选项
      paginator: {}, // 分页
      addDialog: false, // 添加会话框
      editDialog: false, // 编辑会话框
      parentCategory: [], // 用于存放父类别选项
      // 用于存放食品信息
      tableData: [],
      // 用于存放添加项信息
      add_form: {
        parentId: '0',
        name: '',
        categoryDescription: ''
      },
      // 用于存放编辑项信息
      edit_form: {
        id: 0,
        parentId: '0',
        name: '',
        categoryDescription: ''
      },
      // 输入框的规则
      rules: {
        name: [
          { required: true, message: '请输入类别名称', trigger: 'blur' }
        ],
        categoryDescription: [
          { required: true, message: '请输入类别描述', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    const params = {
      delete: false,
      pageSize: 10, // 每页显示多少条
      currentPage: 1, // 当前页数
      orderSegment: 'created_time.desc' // 按照时间降序排列
    }
    axios.get('/goods/category', { params }).then((res) => {
      let parentCategoryArr = []
      res.data.data.map(item => {
        // 当不存在父类别时，
        if (this.parentCategory.length === 0) {
          parentCategoryArr.push({
            id: item.parentId,
            name: item.parentId === 0 ? '全部父类别' : item.category
          })
        } else {
          parentCategoryArr.push({
            id: item.parentId,
            name: item.parentId === 0 ? '全部父类别' : item.category
          })
        }
      })
      let arr = this.arrayUnique(parentCategoryArr, 'id')
      this.parentCategory = arr
    })
    this.requestList(params)
  },
  methods: {
    // 去重（对象去重）
    arrayUnique (arr, name) {
      let hash = {}
      return arr.reduce(function (item, next) {
        hash[next[name]] ? '' : (hash[next[name]] = true && item.push(next))
        return item
      }, [])
    },
    // 发送请求 OK
    requestList (params) {
      axios.get('/goods/category', { params }).then((res) => {
        this.tableData = res.data.data
        this.paginator = res.data.paginator || {totalRecord: 0}
      })
    },
    // 改变父类别  OK
    changeCategor (value) {
      this.parentId = value
    },
    // 添加  OK
    // validate 根据设置的rules进行验证，rules全部通过则验证通过
    add (addform) {
      this.$refs['add_form'].validate((valid) => {
        if (valid) {
          this.parentCategory.forEach(item => {
            if (item.id === this.add_form.parentId) {
              this.add_form.category = item.name
            }
          })
          const params = this.add_form
          axios.post('/goods/add', params).then(() => {
            this.addDialog = false
            Notification.success({
              title: '成功',
              message: `成功新增食品类别！`
            })
            const args = {
              pageSize: this.paginator.pageSize,
              currentPage: this.paginator.currentPage
            }
            this.requestList(args)
            this.add_form = {
              parentId: '0',
              name: '',
              categoryDescription: ''
            }
          })
        } else {
          return false
        }
      })
    },
    // 取消添加 ok
    closeAddDialog () {
      this.addDialog = false
      this.add_form = {
        parentId: '0',
        name: '',
        categoryDescription: ''
      }
    },
    // 分页 ok
    Pagination (val) {
      const params = {
        currentPage: val,
        pageSize: this.paginator.pageSize
      }
      this.requestList(params)
    },
    // 删除 打开移除信息弹框 ok
    openMesBox (row) {
      this.$confirm('此操作将永久删除该项, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.handleClick(row)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 移除 ok
    handleClick (row) {
      const id = row.id
      axios.post('/goods/remove', { id }).then(() => {
        row.visible = false
        const params = {
          ...this.paginator
        }
        this.requestList(params)
      })
    },
    // 查询 OK
    search () {
      const params = {
        name: this.categoryName,
        parentId: this.parentId === 0 ? '' : this.parentId,
      }
      this.requestList(params)
    },
    
    // 打开编辑会话框 ok
    openEditDialog (row) {
      this.editDialog = true
      this.edit_form = row
    },
    // 编辑 ok
    edit (editform) {
      this.$refs['edit_form'].validate((valid) => {
        if (valid) {
          this.parentCategory.forEach(item => {
            if (item.id === this.edit_form.parentId) {
              this.edit_form.category = item.name
            }
          })
          const params = this.edit_form
          axios.put('/goods/edit', params).then((res) => {
            this.editDialog = false
            Notification.success({
              title: '成功',
              message: `成功更新食品类别！`
            })
            const args = {
              pageSize: this.paginator.pageSize,
              currentPage: this.paginator.currentPage
            }
            this.requestList(args)
            this.edit_form = {
              id: 0,
              parentId: '0',
              name: '',
              categoryDescription: ''
            }
          })
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style>
@import '../style/Home'
</style>



