import Mock from 'mockjs' // 引入mock

const Random = Mock.Random // 获取随机数据

// 将url中的查询参数转换为对象形式
function param2Obj (url) {
  const search = url.split('?')[1]
  if (!search) {
    return {}
  }
  return JSON.parse('{"' + decodeURIComponent(search).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g, '":"') + '"}')
}

let List = [
  {
    id: Random.id(),
    name: '玉米',
    categoryDescription: '甜甜的',
    parentId: 101,
    category: '蔬菜'
  },
  {
    id: Random.id(),
    name: '梨子',
    categoryDescription: '香甜可口',
    parentId: 102,
    category: '水果'
  },
  {
    id: Random.id(),
    name: '猪肉',
    categoryDescription: '最喜欢吃的',
    parentId: 103,
    category: '肉类'
  },
  {
    id: Random.id(),
    name: '大米',
    categoryDescription: '一顿不吃都不行',
    parentId: 104,
    category: '粮食'
  },
  {
    id: Random.id(),
    name: '菜籽油',
    categoryDescription: '家家户户必备',
    parentId: 105,
    category: '油类'
  }
] // 存放随机数据的对象

export default {
  /**
   * 获取列表
   * 要带参数 name, page, limt; name可以不填, page,limit有默认值。
   * @param name, page, limit
   * @return {{code: number, count: number, data: *[]}}
   */
  // 查询
  getList: config => {

    const { name = '', currentPage = 1, parentId = '', pageSize = 10 } = param2Obj(config.url)
    const name2 = decodeURIComponent(name)

    const searchList = List.filter(user => {
      if (parentId && parentId != 0 && user.parentId != parentId) return false
      return true
    })

    const mockList = searchList.filter(user => {
      if (name2 && user.name.indexOf(name2) === -1) return false
      return true
    })

    const pageList = mockList.filter((item, index) => index < pageSize * currentPage && index >= pageSize * (currentPage - 1))

    let paginator = {
      currentPage: Number(currentPage),
      pageSize: Number(pageSize),
      totalRecord: mockList.length
    }
    return {
      code: 200,
      paginator,
      data: pageList
    }
  },
  /**
   * 增加用户
   * @param name, addr, age, birth, sex
   * @return {{code: number, data: {message: string}}}
   */
  // 添加
  createUser: config => {
    const { parentId, category = '', name, categoryDescription } = JSON.parse(config.body)
    List.unshift({
      'id': Random.id(),
      parentId,
      category: category === '' ? '全部父类别' : category,
      name,
      categoryDescription
    })
    return {
      code: 200,
      ret: true
    }
  },
  /**
   * 删除用户
   * @param id
   * @return {*}
   */
  // 移除
  deleteUser: config => {
    const { id } = JSON.parse(config.body)
    if (!id) {
      return {
        code: -999,
        message: '参数不正确'
      }
    } else {
      List = List.filter(u => u.id !== id)
      return {
        code: 200,
        ret: true
      }
    }
  },
  /**
   * 修改用户
   * @param id, name, addr, age, birth, sex
   * @return {{code: number, data: {message: string}}}
   */
  // 编辑
  updateUser: config => {
    const { id, parentId, name, categoryDescription, category = '' } = JSON.parse(config.body)
    List.some(u => {
      if (u.id === id) {
        u.parentId = parentId
        u.name = name
        u.categoryDescription = categoryDescription
        u.category = category === '' ? '全部父类别' : category
        return true
      }
    })
    return {
      code: 200,
      ret: true
    }
  }
}



